import React, { useState, } from 'react';
import logo from '../logo.svg';
import './Section.css';
import './Projects.css';
import './Medium.css';
import { Typography, Hidden } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import CloseOutlined from '@material-ui/icons/CloseOutlined'
import NotesOutlined from '@material-ui/icons/NotesOutlined'

const useStyles = makeStyles(theme => ({
  lightFont: {
    fontWeight: 200,
  },
}));

function Medium({ labels, show, handleScroll }) {
  const classes = useStyles();
  const [activeIndex, setActiveIndex] = useState(-1)

  const medium = labels.medium

  const handleProjectClick = (event, index) => {
    event.stopPropagation();
    window.open( medium[index].link, "_blank")
  }

  return (
    <div className={"container container-projects" + (show ? ' container-show' : '')}>
      <div className="medium-grid" onScroll={handleScroll} >
        {
          medium.map((articulo, index) => (
            <div className={"medium-container"} key={index} onClick={(event) => handleProjectClick(event, index)}>
              <div className='medium-img-container'>
                <img src={articulo.img} className="img" alt="logo" />
              </div>
              <div className="medium-text-container">
                <Typography variant='h3' align='left' className='medium-text-title'>{articulo.titulo}</Typography>
                <Typography variant='h5' align='justify' className='medium-text-subtitle'>{articulo.subtitulo}</Typography>
                <Typography variant='body1' align='justify' className={classes.lightFont}>{articulo.time}</Typography>
              </div>
            </div>
          ))
        }
      </div>
    </div>
  );
}

export default Medium;